.motherFrame {
  width: 1200px;
  height: 755px;
  position: absolute;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 8px;
  border: 1px solid rgb(80, 80, 80);
  background: black;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clearMotherFrame {
  position: absolute;
  width: 1200px;
  height: 755px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mainText {
  position: absolute;
  color: white;
  width: 1500px;
  height: 200px;
  line-height: 180px;
  text-align: center;
  font-size: 250px;
  text-decoration: underline dotted 20px;
  text-underline-position: under;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: textOpacity infinite 5s;
}

@keyframes textOpacity {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.onButton {
  position: absolute;
  width: 90px;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-180deg);
  animation: buttonColor infinite 5s;
}

.onButton:hover {
  animation: buttonChange ease-in-out 1s forwards;
}

@keyframes buttonChange {
  0% {
  }
  100% {
    transform: translate(-50%, -50%) rotate(0deg);
    -webkit-filter: opacity(0.5) drop-shadow(0 0 0 rgb(255, 0, 0));
    filter: opacity(0.5) drop-shadow(0 0 0 rgb(255, 0, 0));
  }
}

@keyframes buttonColor {
  0% {
  }
  50% {
    -webkit-filter: opacity(0.5) drop-shadow(0 0 0 rgb(0, 255, 0));
    filter: opacity(0.5) drop-shadow(0 0 0 rgb(0, 255, 0));
  }
  100% {
  }
}

.macBook {
  position: absolute;
  top: 48%;
  left: 50%;
  transform: translate(-50%, -50%) rotateX(90deg);
  transform-origin: bottom;
  width: 1450px;
}

.macBar {
  position: absolute;
  display: none;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1500px;
  height: 50px;
}

.macRotate {
  animation: rotate 3s ease-in-out forwards;
}

@keyframes rotate {
  to {
    transform: translate(-50%, -50%) rotateX(0deg);
  }
}

.loadingFrame {
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 350px;
  height: 8px;
  border-radius: 15px;
  background: #000;
  border: 1px solid gray;
  text-align: center;
}

.loadingFrame > p {
  margin-top: 25px;
  color: gray;
}

.loginFrame {
  position: absolute;
  color: white;
  font-size: 20px;
  text-shadow: 1px 1px 2px gray;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: appear 2s ease-out forwards;
}

.login > div {
  margin: 15px;
}

.loadingBar {
  width: 100%;
  height: 6px;
  margin-top: 1px;
  border-radius: 15px;
  background: white;
  position: absolute;
  box-shadow: 0px 0px 10px 1px white;
  animation: chargingBar 5s ease-out;
}

.logo {
  position: absolute;
  top: -130px;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
}

.password {
  border-radius: 14px;
  width: 180px;
  height: 20px;
}

.porfileImg {
  width: 180px;
}

@keyframes chargingBar {
  0% {
    width: 0px;
  }

  60% {
    width: 40%;
  }

  100% {
    width: 100%;
  }
}

@keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
